.navbar {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    padding: 1.5rem;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    z-index: 100; 
}

.navbar-brand {
    display: block;
    width: fit-content;
}

.navbar-menu {
    margin: 0;
    padding: 0;
    list-style-type: none;
}

.navbar-item {
    display: block;
    color: var(--white);
    text-transform: uppercase;
    font-family: var(--barlowCondensed);
    margin-bottom: 1.5625rem;
}

.navbar-link {
    color: var(--white);
    font-size: 1rem;
    line-height: 1.1875;
    letter-spacing: 2.7px;
    font-family: var(--barlowCondensed);
}

.navbar-link strong {
    display: inline-block;
    margin-right: 0.5625rem;
}

@media screen and (max-width: 767px) {
    .navbar-logo {
        width: 2.5rem;
    }

    .navbar-menu {
        display: none;
        padding: 113px 32px;
        position: fixed;
        top: 0;
        right: 0.25rem;
        bottom: 0;
        width: 250px;
        backdrop-filter: blur(5.125rem);
        background-color: rgba(255, 255, 255, 0.04);

        &.show {
            display: block;
        }
    }
    
    .navbar-toggle { 
        position: relative;
        border: none;
        background: none;
        padding: 0;
    }
}

@media screen and (min-width: 768px) {
    .navbar {
        padding: 0  0  0 2.5rem;
    }

    .navbar-logo {
        width: 3rem;
    }

    .navbar-menu {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 0;
        padding: 0 2.875rem;
        column-gap: 2.3125rem;
        backdrop-filter: blur(5.125rem);
        background-color: rgba(255, 255, 255, 0.04);
    }

    .navbar-item {
        margin-bottom: 0;
        padding-block: 2.125rem 2.0625rem;
        border-bottom: 0.1875rem solid transparent;

        &.active {
            border-color: var(--white);
        }
    }

    .navbar-link {
        font-size: 0.875rem;
        line-height: 1.2142857;
        letter-spacing: 2.36px;
    }

    .navbar-link strong {
        display: none;
    }

    .navbar-toggle {
        display: none;
    }
}

@media screen and (min-width: 1440px) {
    .navbar {
        padding-top: 2.5rem;
        padding-inline: 3.4375rem 0;
    }

    .navbar-menu {
        column-gap: 3.125rem;
        padding-inline: 7.6875rem 10.3125rem;
    }

    .navbar-link {
        font-size: 1rem;
        letter-spacing: 2.7px;
        line-height: 1.1875;
    }

    .navbar-link strong {
        display: inline-block;
    }
}