.page {
    text-align: center;
    min-height: 100vh;
    background-repeat: no-repeat;
    background-size: cover;
}

.home {
    @media screen and (max-width: 767px) {
        padding-block: 113px 47px;
        background-image: url('./../assets/home/background-home-mobile.jpg');
    }

    @media screen and (min-width: 768px) {
        padding-block: 202px 91px;
        background-image: url('./../assets/home/background-home-tablet.jpg');
    }

    @media screen and (min-width: 1440px) {
        text-align: left;
        padding-block: 386px 131px;
        background-image: url('./../assets/home/background-home-desktop.jpg');
    }
}

.page-title {
    font-size: 1rem;
    line-height: 1.15;
    margin-bottom: 1rem;
    letter-spacing: 2.7px;
    font-family: var(--barlowCondensed);

    @media screen and (min-width: 768px) {
        font-size: 1.25rem;
        line-height: 1.2;
        letter-spacing: 3.38px;
        margin-bottom: 1.1em;
    }

    @media screen and (min-width: 1440px) {
        font-size: 1.75rem;
        line-height: 1.25;
        letter-spacing: 4.72px;
        margin-bottom: 1.5rem;
    }
}

.page-number {
    opacity: 0.25;
    display: inline-block;
    margin-right: 1em;

    @media screen and (min-width: 768px) {
        padding-left: 1rem;
        margin-right: 0.875rem;
    }

    @media screen and (min-width: 1440px) {
        padding-left: 0;
        margin-right: 1.5rem;
    }
}

.title-highlight {
    display: block;
    color: var(--white);
    font-family: var(--bellefair);
    letter-spacing: 0;
    font-size: 5em;
    line-height: 1.25;
    margin-top: 1.125rem;

    @media screen and (min-width: 768px) {
        font-size: 7.5em;
        line-height: 1;
        margin-top: 1.5625rem;
    }

    @media screen and (min-width: 1440px) {
        font-size: 9.375rem;
        line-height: 1.1466667;
        margin-top: 1.5rem;
    }
}

.page-desc {
    font-size: 0.9375rem;
    line-height: 1.666667;
    margin-bottom: 5rem;

    @media screen and (min-width:768px) {
        font-size: 1rem;
        line-height: 1.75;        
        max-width: 27.875rem;
        margin-inline: auto;
        margin-bottom: 9.75rem;
    }

    @media screen and (min-width: 1440px) {
        font-size: 1.125rem;
        line-height: 1.77777;
        margin: 0 0;
    }
}

.large-btn {
    font-size: 1.25rem;
    line-height: 1.15;
    letter-spacing: 1.25px;
    position: relative;
    display: inline-grid;
    place-items: center;
    aspect-ratio: 1;
    padding-inline: 1.4em;
    margin-inline: auto;
    border-radius: 50%;
    color: var(--black);
    z-index: 1;
    text-transform: uppercase;
    font-family: var(--bellefair);
    background-color: var(--white);

    @media screen and (min-width: 768px) {
        font-size: 2rem;
        letter-spacing: 2px;
        line-height: 1.15625;
        padding-inline: 1.4375em;
    }

    @media screen and (min-width: 1440px) {
        font-size: 2rem;
        line-height: 1.15625;
        padding-inline: 3.875rem;
    }
}

.large-btn::after {
    content: '';
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
    background: rgba(255,255,255, .1);
    border-radius: 50%;
    opacity: 0;
    transition: opacity 500ms linear, transform 750ms ease-in-out;
}

.large-btn:hover::after,
.large-btn:focus::after {
    opacity: 1;
    transform: scale(1.5);
}

/* DESTINATION */
.destinations {
    text-align: center;

    @media screen and (max-width: 767px) {
        padding-block: 5.5rem 1.875rem;
        background-image: url('./../assets/destination/background-destination-mobile.jpg');
    }

    @media screen and (min-width: 768px) {
        padding-block: 8.5rem 0;
        background-image: url('./../assets/destination/background-destination-tablet.jpg');

        .page-title {
            text-align: left;
        }
    }

    @media screen and (min-width: 1440px) {
        padding-block: 13rem 5.5rem;
        text-align: left;
        background-image: url('./../assets/destination/background-destination-desktop.jpg');
    }
}

.destination-content {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;

    @media screen and (min-width: 1440px) {
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        padding-top: 3.5rem;
    }
}

.destination-figure {
    width: 10.625rem;
    margin: 2rem auto 1.25rem;

    @media screen and (min-width: 768px) {
        width: 18.75rem;
        margin-block: 3.75rem 2.75rem;
    }

    @media screen and (min-width: 1440px) {
        width: auto;
        margin: 0;
        padding: 0 4.125rem;

        .destination-location {
            padding-top: 1.125rem;
        }

    }
}

.destination-nav {
    font-size: 0.875rem;
    line-height: 1.2142857;
    letter-spacing: 2.36px;
    align-items: center;
    justify-content: center;
    column-gap: 1.5625rem;
    margin-bottom: 1.25rem;

    @media screen and (min-width: 768px) {
        font-size: 1rem;
        line-height: 1.1875;
        letter-spacing: 0.16875rem;
        column-gap: 2.125rem;
        margin-bottom: 2rem;

        .menu-item {
            padding-block: 0.625rem;
        }
    }

    @media screen and (min-width: 1440px) {
       justify-content: flex-start;
       margin-bottom: 2.5rem;
    }
}

.destination-wrapper {
    @media screen and (min-width: 1440px) {
        max-width: 27.75rem;
    }
}

.destination-title {
    font-size: 3.5rem;
    line-height: 1.142857;
    margin-bottom: 0;

    @media screen and (min-width: 768px) {
        font-size: 5rem;
        line-height: 1.15;
        margin-bottom: 0.5rem;
    }

    @media screen and (min-width: 1440px) {
        font-size: 6.25rem;
        margin-bottom: 0.875rem;
    }
}

.destination-desc {
    font-size: 0.9375rem;
    line-height: 1.66667;
    margin-bottom: 0;

    @media screen and (min-width: 768px) {
        font-size: 1rem;
        line-height: 1.75;
        max-width: 35.75rem;
        margin-inline: auto;
        margin-bottom: 3rem;
    }

    @media screen and (min-width: 1440px) {
        font-size: 1.125rem;
        line-height: 1.77777;
    }
}

.destination-tabs {
    @media screen and (min-width: 768px) {
        hr {
            max-width: 35.8125rem;
        }
    }
}

.destination-info {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    padding-top: 0.25rem;
    text-transform: uppercase;

    @media screen and (min-width: 768px) {
        padding-top: 0;
        flex-direction: row;
        max-width: 35.8125rem;
        margin-inline: auto;
        justify-content: center;

        .destination-distance {
            width: 14rem;
        }

        .destination-time {
            width: 14rem;
        }
    }

    @media screen and (min-width: 1440px) {
        max-width: none;
        margin-inline: 0;
        padding-top: 0.25rem;
        justify-content: flex-start;

        .destination-distance {
            width: 50%;
        }

        .destination-time {
            width: 50%;
        }
    }
}

.info-label {
    font-size: 0.875rem;
    line-height: 1.2142857;
    letter-spacing: 0.1475em;
    font-family: var(--barlowCondensed);
}

.info-value {
    font-size: 1.75rem;
    line-height: 1.142857;
    color: var(--white);
    font-family: var(--bellefair);
}

/* CREW */
.crew {
    text-align: center;
    background-size: cover;

    @media screen and (max-width: 767px) {
        padding-block: 5.5rem;
        background-image: url('./../assets/crew/background-crew-mobile.jpg');
    }

    @media screen and (min-width: 768px) {
        text-align: left;
        padding-block: 8.5rem 0;
        background-image: url('./../assets/crew/background-crew-tablet.jpg');
    }

    @media screen and (min-width: 1440px) {
        padding-block: 13.25rem 8.6875rem;
        background-image: url('./../assets/crew/background-crew-desktop.jpg');
    }
}

.crew-members {
    position: relative;
}

.crew-member {
    position: relative;

    @media screen and (min-width: 768px) {
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        text-align: center;
    }
}

.crew-member:not(.show) {
    display: none;
}

.crew-member-figure {
    margin: 0 0 4.625rem;
    border-bottom: 0.0625rem solid #383B4B;

    @media screen and (min-width: 768px) {
        order: 2;
        margin: 0;
        border: none;
    }

    @media screen and (min-width: 1440px) {
        position: absolute;
        right: -1.25rem;
        bottom: -8.75rem;
    }
}

.crew-member-image {
    height: 13.875rem;
    object-fit: cover;
    margin: 2.125rem auto 0;

    @media screen and (min-width: 768px) {
        height: 33.25rem;
    }

    @media screen and (min-width: 1440px) {
        height: auto;
        margin: 0;
    }
}

.crew-member-info {
    @media screen and (min-width: 768px) {
        order: 1;
        padding-top: 3.75rem;
        padding-bottom: 2.5rem;
    }

    @media screen and (min-width: 1440px) {
        text-align: left;
        padding-top: 9.5rem;
    }
}

.crew-member-job {
    color: var(--white);
    font-size: 1rem;
    line-height: 1.125;
    opacity: 0.5;
    margin-bottom: 0.5rem;

    @media screen and (min-width: 768px) {
        font-size: 1.5rem;
        line-height: 1.16667;
        margin-bottom: 0.5rem;
    }

    @media screen and (min-width: 1440px) {
        font-size: 2rem;
        line-height: 1.125;
        margin-bottom: 1.125rem;
    }
}

.crew-member-name {
    font-size: 1.5rem;
    line-height: 1.16667;
    margin-bottom: 1rem;

    @media screen and (min-width: 768px) {
        font-size: 2.5rem;
        line-height: 1.15;
        margin-bottom: 1rem;
    }

    @media screen and (min-width: 1440px) {
        font-size: 3.5rem;
        line-height: 1.142857;
        margin-bottom: 1.625rem;
    }
}

.crew-member-desc {
    font-size: 0.9375rem;
    line-height: 1.6667;

    @media screen and (min-width: 768px) {
        font-size: 1rem;
        line-height: 1.75;
        margin-inline: auto;
        max-width: 36.75rem;
    }

    @media screen and (min-width: 1440px) {
        font-size: 1.125rem;
        line-height: 1.77777;
        max-width: 27.75rem;
        margin-inline: 0;
    }
}

.crew-members-nav {
    position: absolute;
    top: 15.875rem;
    left: 0;
    right: 0;
    padding: 0;
    margin: 0;
    list-style: none;  
    display: flex;
    flex-wrap: wrap;
    column-gap: 1rem;
    align-items: center;
    justify-content: center;

    @media screen and (min-width: 768px) {
        top: 17.625rem;
    }

    @media screen and (min-width: 1440px) {
        top: 545px;
        left: 4px;
        right: unset;
        column-gap: 1.5rem;
    }
}

.crew-members-nav li {
    width: 10px;
    height: 10px;
    opacity: 0.2;
    cursor: pointer;
    border-radius: 50%;
    background-color: var(--white);

    @media screen and (min-width: 1440px) {
        width: 15px;
        height: 15px;
    }
}

.crew-members-nav li.active {
    opacity: 1;
}

/* TECHNOLOGY */
.technology {
    text-align: center;
    background-size: cover;

    @media screen and (max-width: 767px) {
        padding-block: 5.5rem 0;
        background-image: url('./../assets/technology/background-technology-mobile.jpg');
    }

    @media screen and (min-width: 768px) {
        text-align: left;
        padding-block: 8.4375rem 1.375rem;
        background-image: url('./../assets/technology/background-technology-tablet.jpg');
    }

    @media screen and (min-width: 1440px) {
        text-align: left;
        padding-block: 13.25rem 10.75rem;
        background-image: url('./../assets/technology/background-technology-desktop.jpg');
    }
}

.technologies-wrapper {
    padding-block: 2rem;

    @media screen and (min-width: 768px) {
        padding-block: 1.5rem;
    }
}

.technologies-list {
    position: relative;
}

.technologies-nav {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    column-gap: 1rem;
    cursor: pointer;
    position: absolute;
    top: 12.625rem;
    left: 50%;
    transform: translate(-50%, 0);

    @media screen and (min-width: 768px) {
        top: 23rem;
    }

    @media screen and (min-width: 1440px) {
        column-gap: 0;
        row-gap: 2rem;
        flex-direction: column;
        top: 7rem;
        left: 0;
        transform: none;
    }
}

.technologies-nav li {
    width: 2.5rem;
    border-radius: 50%;
    text-align: center;
    line-height: 1.125;
    padding: 0.625rem;
    font-family: var(--bellefair);
    border: 0.0625rem solid rgba(255,255,255, .25);

    @media screen and (min-width: 768px) {
        font-size: 1.5rem;
        line-height: 1.16667;
        width: 3.75rem;
        padding: 0.9375rem;
    }

    @media screen and (min-width: 1440px) {
        width: 5rem;
        font-size: 2rem;
        line-height: 1.125;
        padding: 1.3125rem;
    }
}

.technologies-nav .active {
    color: var(--black);
    background-color: var(--white);
}

.technology-item {
    position: relative;
    text-align: center;

    @media screen and (min-width: 1440px) {
        padding-left: 10rem;
        text-align: left;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
    }
}

.technology-item:not(.show) {
    display: none;
}

.technology-figure {
    margin: 0 -1.5rem 6.25rem;

    @media screen and (min-width: 768px) {
        margin-bottom: 10.125rem;
    }

    @media screen and (min-width: 1440px) {
        position: absolute;
        top: 0;
        right: -8.75rem;
    }
}

.technology-image {
    width: 100%;
    object-fit: cover;
    height: 10.625rem;

    @media screen and (min-width: 768px) {
        height: auto;
    }
}

.technology-info {
    order: 1;
    margin-top: 7rem;
}

.technology-heading {
    font-family: var(--barlowCondensed);
    font-size: 0.875rem;
    line-height: 1.2142857; 
    letter-spacing: 0.1475rem;
    margin-bottom: 0.625rem;

    @media screen and (min-width: 768px) {
       font-size: 1rem;
       line-height: 1.1875;
       letter-spacing: 0.16875rem;
       margin-bottom: 1rem;
    }

    @media screen and (min-width: 1440px) {
        margin-bottom: 0.625rem;
    }
}

.technology-title {
    color: var(--white);
    font-size: 1.5rem;
    line-height: 1.16667;

    @media screen and (min-width: 768px) {
        font-size: 2.5rem;
        line-height: 1.15;
        margin-bottom: 0.875rem;
    }

    @media screen and (min-width: 1440px) {
        font-size: 3.5rem;
        line-height: 1.142857;
        margin-bottom: 1.125rem;
    }
}

.technology-desc {
    font-size: 0.9375rem;
    line-height: 1.66667;

    @media screen and (min-width: 768px) {
        font-size: 1rem;
        line-height: 1.75;
        margin-inline: auto;
        max-width: 27.8125rem;
    }
    
    @media screen and (min-width: 1440px) {
        font-size: 1.125rem;
        line-height: 1.77777;
        margin-inline: 0;
        max-width: 27.75rem;
    }
}