* {
  box-sizing: border-box;
}

:root {
  --primary: #d0d6f9;
  --black: #0b0d17;
  --white: #ffffff;
  --gray: #979797;
  --barlowCondensed: 'Barlow Condensed', sans-serif;
  --barlowRegular: 'Barlow', sans-serif;
  --bellefair: 'Bellefair', sans-serif;
}

body {
  color: var(--primary);
  font-size: 16px;
  line-height: 1.625;
  font-family: var(--barlowRegular);
  background-color: var(--black);
}

h1, h2, h3, h4, .h1, .h2, .h3, .h4 {
  color:var(--primary);
  text-transform: uppercase;
  margin: 0 0 1rem;
  font-weight: 400;
}

h1, .h1 {
  font-family: var(--bellefair);
  font-size: 8.33333rem;
}

h2, .h2 {
  font-family: var(--bellefair);
  font-size: 5.55555rem;
}

h3, .h3 {
  font-family: var(--bellefair);
  font-size: 3.11111rem;
}

h4, .h4 {
  font-family: var(--bellefair);
  font-size: 1.77777rem;
}

h5, .h5 {
  font-family: var(--barlowCondensed);
  font-size: 28px;
  letter-spacing: 4.75px;
}

.sh1 {
  font-family: var(--bellefair);
  font-size: 28px;
}

.sh2 {
  font-family: var(--barlowCondensed);
  font-size: 14px;
  letter-spacing: 2.35px;
}

p, .p {
  font-family: inherit;
  font-size: 1.125em;
  line-height: 1.77777777;
  margin: 0 0 1em;
}

img {
  display: block;
  max-width: 100%;
}

button {
  padding: 0;
  border: none;
  cursor: pointer;
  background: none;
  appearance: none;
}

ul {
  margin: 0 0 1em;
  padding-left: 1em;
}

a {
  text-decoration: none;
  color: var(--primary);
}

.menu {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
}

.menu-item {
  text-transform: uppercase;
  font-family: var(--barlowCondensed);
}

.menu-item {
  padding-block: 0.5rem;
  border-bottom: 0.1875rem solid transparent
}

.menu-item.active {
  border-color: var(--white);
}

.tab {
  display: none;
}

.tab.show {
  display: block;
}

hr {
  border: none;
  height: 0.0625rem;
  margin-block: 1.875rem;
  background-color: var(--gray);
}

.ff-barlow {
  font-family: var(--barlowRegular);
}

.ff-bellefair {
  font-family: var(--bellefair);
}

.ff-barlow-condensed {
  font-family: var(--barlowCondensed);
}


.mb-0 {
  margin-bottom: 0!important;
}

.container {
  position: relative;
  padding-inline: 1.5rem;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-inline: -1.5rem;
}

.align-items-center {
  align-items: center;
}

.align-items-end {
  align-items: flex-end;
}

.justify-content-between {
  justify-content: space-between;
}

[class^="col-"] {
  padding-inline: 1.5rem;
}

.col-12 {
  flex: 0 0 100%;
  max-width: 100%;
}

.desktop-image {
  display: none;
}

@media screen and (min-width: 1440px) {
  .container {
    max-width: 72.5rem;
    margin-inline: auto;
  }

  .col-lg {
    flex: 0 0 auto;
  }

  .mobile-image {
    display: none;
  }

  .desktop-image {
    display: block;
  }
}
